html {
    scroll-behavior: smooth;
}

.nav-item {
    border: 1px solid black;
    padding: 0% 2%;
    margin: 5% 0% 0% 5%;
    box-shadow: 5px 5px grey;
    font-family: 'Abel', sans-serif;
    font-size: 18px;
    background-color:white;
}

.navbar-container {
    margin-right: 0%;
}

.home-button {
padding: 0% 3%;
}

.navbar {
    background-color: white;
    border-bottom: 1px solid grey;
    padding-bottom: 1%;
    position: fixed;
    top: 0;
    width: 100%;
    z-index:1;
}

@media only screen and (max-width: 600px) {
    .navbar {
    padding-bottom: 2%;
    }
}

@media only screen and ( max-width: 1210px) {
    .navbar {
        padding-bottom: 1%;
    }
    .navbar-container {
    margin-left:0%;
    }
}
#About {
padding-bottom: 0;
}

.about-container {
    max-width: 80%;
    margin:8% 5% 5%;
}

.about-title {
font-family: 'Mr Bedfort', cursive;
font-size: 3rem;
}

.about-text {
line-height: 1.75rem;
}

.about-signature {
float:right;
}


about-image {
width: 10px;
}

.about-sunflower {
    position:relative;
    bottom: 50px;
    margin-left: -15%;
    -webkit-transform: rotate(-30deg);
            transform: rotate(-30deg);
}

.about-container {
    margin-top:13%;
}


.profile-container {
padding-right: 12%;
padding-bottom: 10%;
}

.profile-name {
    font-family: 'Satisfy', cursive;
    font-size: 3rem;
}

.background-image {
background-size: cover;
}
.work-title {
    font-family: 'Libre Franklin', sans-serif;
    text-align: center;
    padding-top: 5%;
}

.resume-container {
padding: 0% 12%;

}

.resume-title {
    font-family: 'Heebo', sans-serif;
    font-weight: bold;
}

.text-dark {
    text-align: left;
}

hr {
    margin: 3% auto;
    border: 8px dotted #808080;
    border-bottom: none;
    width: 3%;
}

